import React from "react";
import { useParams } from "react-router-dom";

//helper
import RenderizarPlanPorID from "./Helper/RenderizarPlanPorID";

const PlanGeinsoft = () => {
  //obtener el id de la url
  let { id } = useParams();

  //Condicionar Rendering
  return <RenderizarPlanPorID id={id} />;
};

export default PlanGeinsoft;
