import React, { useState, useEffect } from "react";
import Logo from "../assets/logos/imagotipo-color.svg";
const Navbar = () => {
  const [navbar, guardarNavbar] = useState(false);
  const [hamburgesa, guardarhamburgesa] = useState(false);
  const [mostrar, guardarMostrar] = useState(true);
  const [movil, guardarMovil] = useState(false);

  window.addEventListener("scroll", () => {
    let contador = window.scrollY;
    if (contador >= 80) {
      guardarNavbar(true);
    } else {
      guardarNavbar(false);
    }
  });
  let ancho = window.innerWidth;
  useEffect(() => {
    if (ancho >= 800) {
      guardarMostrar(true);
      console.log(mostrar);
    } else {
      guardarMostrar(false);
    }
  }, []);

  function cambiar() {
    if (hamburgesa === true) {
      guardarhamburgesa(false);
      guardarMovil(false);
    } else {
      guardarhamburgesa(true);
      guardarMovil(true);
    }
  }

  return (
    <header className={navbar ? "header" : "header2"}>
      <img src={Logo} className="navbar-logo" alt="geinsoft" />

      <nav
        className={`navbar ${movil ? "movil" : null} ${
          mostrar ? null : "ocultar"
        }`}
      >
        <ul className="navbar-links">
          <li className="navbar-li">
            <a className="navbar-a" href="/">
              HOME
            </a>
          </li>
          <li className="navbar-li">
            <a className="navbar-a" href="#trabajos">
              PROYECTOS
            </a>
          </li>
          <li className="navbar-li">
            <a className="navbar-a" href="#servicios">
              SERVICIOS
            </a>
          </li>
          <li className="navbar-li">
            <a className="navbar-a" href="#contacto">
              CONTACTO
            </a>
          </li>
        </ul>
      </nav>
      <button
        onClick={() => cambiar()}
        id="hamburger"
        className={`hamburger hamburger--collapse ${
          hamburgesa ? "is-active" : null
        } ${mostrar ? "ocultar" : null}`}
        type="button"
      >
        <span className="hamburger-box">
          <span className="hamburger-inner"></span>
        </span>
      </button>
    </header>
  );
};

export default Navbar;
