import React from "react";
import Fondo from "../../assets/img/bg-2.svg";
import Img from "../../assets/img/escena-2.svg";
const Necesidades = () => {
  return (
    <section className="necesidades">
      <div className="necesidades-izq">
        <img className="necesidades-fondo" src={Fondo} alt="" />
        <img className="necesidades-img" src={Img} alt="" />
      </div>
      <div className="necesidades-der">
        <h2>
          Hacemos nuestras tus necesidades, estamos contigo en todo el proceso.
        </h2>
      </div>
    </section>
  );
};

export default Necesidades;
