import React from "react";

import logoGeinsoft from "../assets/logos/imagotipo-blanco.svg";
import imgCorreo from "../assets/icons/correo.svg";
import imgTelefono from "../assets/icons/telefono.svg";

const Footer = () => {
  return (
    <section className="footer">
      <div className="footer-container">
        <div className="footer-correo">
          <img src={imgCorreo} alt="imagen-correo" />
          <p>contacto@geinsoft.com</p>
        </div>
        <img className="footer-logo" src={logoGeinsoft} alt="logo geinsoft" />
        <div className="footer-telefono">
          <img src={imgTelefono} alt="imagen-telefono" />
          <p>+57 3045809637</p>
        </div>
      </div>
    </section>
  );
};

export default Footer;
