import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Home from "./components/Home/Index";
import PlanGeinsoft from "./components/Plan";

//Aqui colocaremos las rutas, Ya esta la de Home (Inicio)
//Tambien tenemos los planes
//Puedes guiarte del modelo que use para los planes para que lo apliques en el portafolio.

const Routes = () => {
  return (
    <>
      <Router>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/plan/:id" component={PlanGeinsoft} />
        </Switch>
      </Router>
    </>
  );
};

export default Routes;
