import React from "react";

import EcommercePlan from "../EcommercePlan";
import CMSPlan from "../CMSPlan";
import SoftwareMedidaPlan from "../SoftwareMedidaPlan";
import MultiPlataformaPlan from "../MultiPlataformaPlan";

const RenderizarPlanPorID = ({ id }) => {
  switch (id) {
    case "0":
      return <EcommercePlan />;
    case "1":
      return <CMSPlan />;
    case "2":
      return <SoftwareMedidaPlan />;
    case "3":
      return <MultiPlataformaPlan />;
    default:
      return <h2>Cargando</h2>;
  }
};

export default RenderizarPlanPorID;
